// https://developer.spotify.com/documentation/web-api/quick-start/#

export const authEndpoint = 'https://accounts.spotify.com/authorize';
const clientId = '8af4f87afe1b4c8ba510eef37c1333b8'
const redirectUri = 'https://spotify-clone-d1141.web.app/'

const scope = [
  'user-read-currently-playing',
  'user-read-recently-played',
  'user-read-playback-state',
  'user-top-read',
  'user-modify-playback-state'
]

export const getTokenFromUrl = () => {
  return window.location.hash
    .substring(1)
    .split('&')
    .reduce((initial, item) => {
      let parts = item.split('=')
      initial[parts[0]] = decodeURIComponent(parts[1])
      return initial
    }, {})
}

export const loginUrl = `${authEndpoint}?client_id=${clientId}&redirect_uri=${redirectUri}&scope=${scope.join('%20')}&response_type=token&show_dialog=true`;
